
.thx {
	.layout {
		@include r(1200) {
			padding: 0 20px; } }
	.hero {
		background: #0d0d0d url('../img/content/black-bg5.jpg');
		&__content {
			text-align: center;
			position: static;
			@include r(1100) {
				width: 100%;
				height: auto;
				padding-bottom: 60px; } }
		&__icon {
			margin-bottom: 30px;
			transform: rotate(-45deg) scale(.1);
			@include tr(.6s);
			@include r(650) {
				height: 100px; } }
		&__bottom {
			margin-top: 80px;
			position: relative;
			cursor: pointer;
			&:after {
				content: '';
				position: absolute;
				top: 110%;
				left: 50%;
				width: 54px;
				height: 70px;
				background-image: url('../img/content/n-arrow.png');
				background-size: cover;
				transform: translate(-50%, 0) rotate(90deg); } }
		.title__main {
			font-size: 45px;
			@include r(650) {
				font-size: 32px; }
			&:after {
				content: none; } } }
	&.is-loaded {
		.hero__icon {
			transform: rotate(0deg) scale(1); } }
	.footer {
		background: #fff url(../img/content/wh-bg.jpg) repeat;
		background-position-x: -6px;
		&__list {
			a {
				color: #000; } }
		p {
			color: #000; } }
	.f-socio svg {
		fill: $red; } }

.time {
	padding: 100px 0 20px;
	overflow: hidden;
	@include r(1000) {
		padding: 60px 0 20px; }
	.video {}
	.justified {
		margin-bottom: 30px;
		color: #000;
		@include r(650) {
			margin-bottom: 0px; }
		&__content {
			width: 100%; }
		&__item {
			margin-bottom: 30px;
			@include r(650) {
				margin-bottom: 60px; } } }
	h3 {
		font-size: 24px;
		color: #4d4d4d;
		@include msb;
		&:after {
			content: none; }
		mark {
			font-size: 20px; } }
	h2 {
		font-size: 24px;
		text-transform: uppercase;
		color: #2a2a2a;
		@include mbl; }
	.price {
		color: $red;
		text-transform: none;
		font-size: 36px;
		@include mbl;
		@include r(1400) {
			font-size: 26px; }
		@include r(650) {
			margin: 0 auto;
			text-align: center; } }
	.border {
		padding: 40px;
		border: 2px solid $yellow;
		@include r(1300) {
			padding: 40px 10px; }
		@include r(1024) {
			max-width: 650px;
			margin: 0 auto; }
		.justified {
			align-items: center;
			margin-bottom: 0; } } }

.list-star {
	padding: 0;
	margin: 0;
	list-style: none;
	font-size: 0;
	@include r(650) {
		margin: 0 auto; }
	li {
		position: relative;
		margin-bottom: 20px;
		padding-left: 30px;
		font-size: 20px;
		color: #4d4d4d;
		@include mr;
		&:nth-of-type(2n) {
			@include mb; }
		&:before {
			content: '';
			position: absolute;
			top: 0.1em;
			left: 0;
			display: inline-block;
			width: 16px;
			height: 16px;
			background-image: url('../img/content/star.png');
			background-size: cover; } } }

.method {
	display: block;
	width: 100%;
	text-align: center;
	margin: 0 0 0 40px;
	max-width: 346px;
	@include r(1000) {
		margin: 0 auto; }
	&__field {
		position: relative;
		border: 1px solid #a9a9a9;
		border-radius: 20px;
		margin-bottom: 10px;
		padding: 14px; }
	&__pic {
		display: flex;
		justify-content: space-around;
		align-items: center;
		margin-top: 30px; }
	.btn {
		margin-top: 30px; } }

.check {
	position: relative;
	display: inline-block;
	input {
		position: absolute;
		top: 2px;
		right: 0;
		z-index: 2;
		opacity: 0;
		&:checked {
			~ span {
				user-select: none;
				&:before {
					background-color: $n; }
				&:after {
					content: "";
					position: absolute;
					top: 3px;
					right: 4px;
					display: inline-block;
					vertical-align: top;
					width: 10px;
					height: 10px;
					border-radius: 100%;
					background-color: $red;
					// background-image: url('../img/content/icon-selector-checked.png')
					// @include bg(icon-selector-checked)
 } } }					// +s(icon-selector-checked)
		&:disabled {
			~ span {
				opacity: 1; } } }
	span {
		position: relative;
		padding-right: 28px;
		right: 0;
		top: 0;
		font-size: 14px;
		color: #adacac;
		cursor: pointer;
		user-select: none;
		@include mr;
		z-index: 15;
		@include r(380) {
			display: inline-block;
			max-width: 88vw; }
		&:before {
			content: "";
			position: absolute;
			top: -1px;
			right: 0;
			width: 18px;
			height: 18px;
			background-color: $n;
			border-radius: 100%;
			border: 1px solid #535353;
			cursor: pointer;
			@include tr(.4s); } } }


.flip-clock-wrapper {
	margin: 0;
	@include r(1024) {
		width: 100%;
		max-width: 520px;
		margin: 0 auto; }
	@include r(460) {
		width: calc(100% + 10px);
		margin-left: -10px; } }
.flip-clock-label {
	display: none; }
.flip-clock-wrapper a {
	color: #fff; }
.flip-clock-wrapper ul {
	@include r(1470) {
		width: 50px; }
	@include r(1300) {
		width: 40px; }
	@include r(1024) {
		width: 50px; }
	@include r(560) {
		width: 24px;
		height: 50px;
		font-size: 50px;
		line-height: 66px;
		margin: 2px;
		padding: 16px; }
	@include r(400) {
		padding: 10px;
		font-size: 40px; } }
.flip-clock-wrapper ul li {
	@include r(560) {
		line-height: 56px; } }
.flip-clock-wrapper ul li a div div.inn {
	@include r(560) {
		font-size: 46px; }
	@include r(400) {
		font-size: 36px; } }
.flip-clock-divider {
	@include r(1050) {
		width: 10px; }
	@include r(560) {
		height: 44px; } }
.flip-clock-dot {
	@include r(1050) {
		left: 0; }
	&.bottom {
		@include r(560) {
			bottom: 22px; } } }


.flip-clock-wrapper ul.play li.flip-clock-active .up .shadow {
	background-color: $yellow; }
.flip-clock-wrapper ul.play li.flip-clock-active .down .shadow {
	background-color: $yellow; }


.flip-clock-wrapper ul.play li.flip-clock-before .up .shadow {
	background-color: $yellow; }
.flip-clock-wrapper ul.play li.flip-clock-before .down .shadow {
	background-color: $yellow; }

.dots-list {
	margin: 0 0 30px;
	padding: 0;
	list-style: none;
	li {
		position: relative;
		display: block;
		padding-left: 16px;
		font-size: 14px;
		line-height: 1.6;
		color: #0c0c0c;
		@include mr;
		&:before {
			content: '';
			position: absolute;
			top: .6em;
			left: 2px;
			display: inline-block;
			vertical-align: top;
			width: 5px;
			height: 5px;
			background: $red;
			@include rotate(45); } } }

.mod-mb-40 {
	margin-bottom: 40px; }

.text-center {
	text-align: center; }
